@charset "utf-8";

body {
  font-family: 'Nunito Sans', sans-serif;
  background-color: #1a1924;
}

.container {
  width: min(100% - 30px, 1200px);
  margin-inline: auto;
}

.grid {
  display: grid;
  place-items: center;
  height: 100vh;
}

.search-field {
  margin-bottom: 200px;
}

.search-field h1 {
  font-weight: 600;
  font-size: 40px;
  color: #fff;
  text-align: center;
  margin-bottom: 20px
}

input[type=text] {
  line-height: 42px;
  height: 42px;
  width: 300px;
  padding-left: 10px;
  font-size: 16px;
  font-weight: 400;
  margin-right: 20px;
  border-radius: 5px;
}

button {
  line-height: 42px;
  height: 42px;
  width: 100px;
  background-color: #cf5e10; /* #ee7624 */
  font-size: 16px;
  text-transform: uppercase;
  border-radius: 5px;
  color: #fff;
  font-weight: 550;
}

.search-field p {
  height: 42px;
  width: 300px;
  margin-top: 5px;
  color: #f00;
  font-size: 16px;
  font-weight: 600;
}